@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;600&display=swap");
* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(135deg, white, #fff5f5);
  min-height: 100vh;
  margin: 0;
  display: grid;
  grid-template-columns: auto 42%;
  grid-template-rows: min-content min-content 1fr;
  font-size: 16px;
  font-family: "Josefin Sans", sans-serif;
}
@media screen and (min-width: 768px) {
  body {
    overflow: hidden;
  }
}

.logo {
  grid-row: 1;
  grid-column: 1/span 2;
  width: 100px;
  margin: 32px;
}
@media screen and (min-width: 768px) {
  .logo {
    grid-column: 1;
    margin: 66px 20%;
    width: 160px;
  }
}

.hero-image {
  grid-row: 2;
  grid-column: 1/span 2;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .hero-image {
    grid-column: 2;
    grid-row: 1/span 3;
    justify-self: start;
    min-width: 100%;
    min-height: 100vh;
    width: auto;
  }
}

.content {
  grid-row: 3;
  grid-column: 1/span 2;
  color: #ce9797;
  margin: 0 32px;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .content::before {
    background: url("../images/bg-pattern-desktop.svg"), linear-gradient(135deg, white, #fff5f5);
    height: 100vh;
    width: 58%;
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background-size: cover;
  }
}
@media screen and (min-width: 768px) {
  .content {
    grid-column: 1;
    text-align: left;
    margin: 0 20% 0 20%;
  }
}
.content__title {
  margin: 65px 0 14px 0;
  text-transform: uppercase;
  font-weight: 300;
  word-spacing: 100vw;
  font-size: 37px;
  letter-spacing: 0.244em;
  line-height: 1.2;
}
@media screen and (min-width: 768px) {
  .content__title {
    font-size: 48px;
    margin-top: 0;
  }
}
.content__title--brown {
  color: #413a3a;
  font-weight: 600;
}
.content__paragraph {
  line-height: 1.4;
  letter-spacing: -0.037em;
}
@media screen and (min-width: 768px) {
  .content__paragraph {
    line-height: 1.6;
    margin: 25px 0;
  }
}
.content__signin {
  position: relative;
}
.content__signin--invalid .content__signin-input {
  border-color: #f96262;
}
.content__signin--invalid .content__signin-error {
  display: block;
}
.content__signin--invalid .content__signin-button::before {
  display: inline;
}
.content__signin-input {
  width: 100%;
  border: 1px solid #ce9797;
  border-radius: 24px;
  padding: 16px 24px;
}
.content__signin-input::placeholder {
  font-family: "Josefin Sans", sans-serif;
  color: #ce9797;
}
.content__signin-button {
  position: absolute;
  right: 0;
  border: 0;
  height: 50px;
  width: 65px;
  background: linear-gradient(135deg, #f8bfbf, #ee8c8c);
  border-radius: 24px;
  cursor: pointer;
}
.content__signin-button::before {
  content: url("../images/icon-error.svg");
  position: absolute;
  right: 70px;
  display: none;
}
.content__signin-error {
  display: none;
  margin-top: 5px;
  color: #f96262;
}

footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}